.featured{
    width: 100%;
    display: flex;
    justify-content: space-between;
}
.featuredItem{
flex:1;
margin: 0px 20px;
border-radius: 10px;
padding: 30px;
cursor: pointer;
box-shadow:0px 0px 15px -10px rgb(0,0,0);
-webkit-box-shadow:0px 0px 15px -10px rgb(0,0,0);
 }
.featuredItem{
    font-style: 20px;
}
.featuredMoneyContainer{
    margin: 10px 0px;
    display: flex;
    align-items: center;
}
.featuredMoney{
    font-size: 30px;
    font-weight: 600;
}
.featuredMoneyRate{
     display: flex;
     align-items: center;
     margin-left: 20px;
}
.featuredIcon{
    font-style: 14px;
    margin-left: 5px;
    color: green;
}
.featuredIcon.negative{
 
color: red;
}

.featuredSub{
    font-style: 15px;
    color: grey;
}