.topbar{
    width: 100%;
    height: 50px;
    background-color: white;
    top: 0;
    position: sticky;
    z-index: 999;

}
.topbarWrapper{
    height: 100%;
    padding: 0px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.logo{
    font-weight: bold;
    font-style: 30px;
    color: darkblue;
    cursor: pointer;
}
.title{
    font-weight: bold;
    font-size: 24px;
    color: snow;
    cursor: pointer;
}
.topRight{
    display: flex;
    align-items: center;

}
.topbarIonContainer{
    position: relative;
    cursor:pointer;
    color: #555;
}
.topIconBadge{
    width: 15px;
    height: 15px;
    position: absolute; 
    top:-5px;
    right: 0px;
    background-color: red;
    color: white;
    border-radius: 50%; 
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 10px;
}
.topAvatar{
    width: 30px;
    height: 30px;
    border-radius: 50%;
    cursor: pointer;
}
.topbarLink{
 text-decoration: none;
 color: #000;
}
