.widgetLg{
    flex: 2;
    box-shadow:0px 0px 15px -10px rgb(0,0,0.75); ;
    -webkit-box-shadow:0px 0px 15px -10px rgb(0,0,0.75);
    padding: 20px;
    height: 80vh;
    overflow: scroll;
}
.wdigetLgTitle{
  font-size: 22px;
    font-weight: 600;
}
.widgetLgImg{
  width: 40px;
  height: 40px;
  border:1px solid;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 10px;
}
.widgetLgProduct{
  font-size: smaller;
}
.widgetLgDate,.widgetLgAmount{
  font-weight: 300;
}
.widgetLgButton{
  padding: 5px 7px;
  border: none;
  border-radius: 10px;
}
.widgetLgButton.Approved{
  background-color: #e5faf2;
  color:#3bb077 ;
}
.widgetLgButton.Declined{
  background-color: #fff0f1;
  color:#d95087 ;
}
.widgetLgButton.Pending{
  background-color: #ebf1fe;
  color:#2a7ade ;
}
.widgetLgTable{
  width: 100%;
  border-spacing: 20px;
}
.widgetLgTh{
  text-align: start;
}
.widgetLgUser{
  display: flex;
  align-items: center;
  font-weight: 600;
}