.sidebar{
    flex: 1;
    height: calc(100vh - 50px);
    background-color: rgb(251, 251, 255);
    position: sticky;
    top:50px;
   
}

.sidebarWrapper{
    padding: 20px;
    color: #555;
}
.sidebarMenu{
    margin-bottom: 10px;
}
.sidebarTitle{
    font-style: 13px;
    color: rgb(187,186,186);
}
.sidebarlink{
    text-decoration: none;
    color: #555;
}
.sidebarList{
    list-style: none;
    padding: 5px;
}
.sidebarListItem{
    padding: 5px;
    cursor: pointer;
    display: flex;
    align-items: center;
    border-radius: 10px;
} 
.sidebarListItem.active,.sidebarListItem:hover{
    background-color: rgb(228, 228, 250);
}

.sidebarIcon{
margin-right: 5px;
font-size: 20px !important;
}

